import update from "immutability-helper";
import psystatusInitialState from "./psystatusInitialState";

function getInitialState() {
  let firstStatus = { ...psystatusInitialState };
  let date = new Date();
  firstStatus.id = date.getTime();
  firstStatus.isOpen = true;
  let initialState = [firstStatus];
  return initialState;
}

function getOpenStatusIndex(state) {
  let openStatusIndex;
  state.forEach((item, i) => {
    if (item.isOpen === true) {
      openStatusIndex = i;
    }
  });
  return openStatusIndex;
}

// ÑÐ¾Ð±Ð¸ÑÐ°ÐµÑ Ð½Ð¾Ð²ÑÐ¹ ÑÐ¿Ð¸ÑÐ¾Ðº  Ð¿Ð¾ÑÐ»Ðµ ÑÐ´Ð°Ð»ÐµÐ½Ð¸Ñ Ð¾Ð´Ð½Ð¾Ð³Ð¾ Ð¸Ð· ÑÑÐ°ÑÑÑÐ¾Ð²
function getStateAfterDeliteStatus(state, action) {
  let ArrWithDeliteStatus = state.filter(item => +item.id !== +action.id);
  let isAnyOpenStatus = false;
  ArrWithDeliteStatus.forEach(item => {
    if (item.isOpen === true) {
      isAnyOpenStatus = true;
    }
  });
  if (!isAnyOpenStatus) {
    ArrWithDeliteStatus[0].isOpen = true;
  }
  return ArrWithDeliteStatus;
}

// Ð¾ÑÐ½Ð¾Ð²Ð½Ð¾Ð¹ ÑÐµÐ´ÑÑÑÐµÑ, ÑÐ¿ÑÐ°Ð²Ð»ÑÐµÑ Ð´ÐµÐ¹ÑÑÐ²Ð¸ÑÐ¼Ð¸ ÑÐ¾ ÑÐ¿Ð¸ÑÐºÐ¾Ð¼ ÑÑÐ°ÑÑÑÐ¾Ð² Ð¸ Ð²Ð½ÑÑÑÐ¸ Ð¾Ð´Ð½Ð¾Ð³Ð¾ ÑÑÐ°ÑÑÑÐ°
export default function repository(state = getInitialState(), action) {
  let openStatusIndex = getOpenStatusIndex(state);

  // ÑÐ¿ÑÐ°Ð²Ð»ÐµÐ½Ð¸Ðµ ÑÐ¿Ð¸ÑÐºÐ¾Ð¼ ÑÑÐ°ÑÑÑÐ¾Ð²
  switch (action.type) {
    case "CREATE_NEW_STATUS":
      let psystatusBlank = { ...psystatusInitialState };
      psystatusBlank.id = +action.id;
      return update(state, {
        $push: [psystatusBlank]
      });

    case "DELETE_STATUS":
      let stateWithDeliteStatus = getStateAfterDeliteStatus(state, action);

      return update(state, {
        $set: stateWithDeliteStatus
      });

    case "OPEN_STATUS":
      let stateWithOpenStatus = state.map(item => {
        item.isOpen = false;
        if (+item.id === +action.id) {
          item.isOpen = true;
        }
        return item;
      });
      return update(state, {
        $set: stateWithOpenStatus
      });

    case "CLOSE_STATUS":
      let stateWithCloseStatus = state.map(item => {
        item.isOpen = false;
        return item;
      });
      return update(state, {
        $set: stateWithCloseStatus
      });

    // ÑÐ¿ÑÐ°Ð²Ð»ÐµÐ½Ð¸Ðµ Ð²Ð½ÑÑÑÐ¸ Ð¾Ð´Ð½Ð¾Ð³Ð¾ ÑÑÐ°ÑÑÑÐ°

    // Ð¾Ð±Ð½Ð¾Ð²Ð»ÐµÐ½Ð¸Ðµ Ð±ÑÐ»ÐµÐ²Ð° ÑÐ¸Ð¼Ð¿ÑÐ¾Ð¼Ð°
    case "SET_SYMPTOM":
      return update(state, {
        [openStatusIndex]: {
          [action.name]: { isChecked: { $apply: boolean => !boolean } }
        }
      });

    // Ð¾Ð±Ð½Ð¾Ð²Ð»ÐµÐ½Ð¸Ðµ ÑÐµÐºÑÑÐ¾Ð²Ð¾Ð³Ð¾ ÑÐ¸Ð¼Ð¿ÑÐ¾Ð¼Ð°
    case "SET_TEXT":
      return update(state, {
        [openStatusIndex]: { [action.name]: { text: { $set: action.text } } }
      });

    // Ð¾Ð±Ð½Ð¾Ð²Ð»ÐµÐ½Ð¸Ðµ ÑÐ¸Ð¼Ð¿ÑÐ¾Ð¼Ð°, Ð²ÑÐ±Ð¾ÑÐ°Ð½Ð½Ð¾Ð³Ð¾ Ð¸Ð· ÑÐ¿Ð¸ÑÐºÐ° (ÑÑÑÐ°Ð½Ð¾Ð²ÐºÐ° ÐµÐ³Ð¾ Ð½Ð¾Ð¼ÐµÑÐ° Ð² ÐÐ)
    case "SET_RADIO":
      return update(state, {
        [openStatusIndex]: {
          [action.name]: { number: { $set: action.number } }
        }
      });

    default:
      return state;
  }
}
