import rootReducer from "./reducers";
import { composeWithDevTools } from "redux-devtools-extension";
import { createStore, applyMiddleware } from "redux";
import thunk from "redux-thunk";

export default function configureStore(islocalStorage) {
  let store;

  // islocalStorage - ÐµÑÐ»Ð¸ true, Ð¿Ð¾Ð´ÐºÐ»ÑÑÐ°ÐµÑÑÑ Ð¿Ð¾Ð´ÐºÐ»ÑÑÐµÐ½Ð¸Ðµ ÑÐ¾ÑÑÐ°Ð½ÐµÐ½Ð¸Ñ Ð² localStorage
  if (islocalStorage) {
    // Ð¿Ð¾Ð´ÐºÐ»ÑÑÐµÐ½Ð¸Ðµ ÑÐ¾ÑÑÐ°Ð½ÐµÐ½Ð¸Ñ Ð² localStorage
    if (localStorage["mentatus"]) {
      store = createStore(
        rootReducer,
        JSON.parse(localStorage["mentatus"]),
        composeWithDevTools(applyMiddleware(thunk))
      );
    } else {
      store = createStore(
        rootReducer,
        composeWithDevTools(applyMiddleware(thunk))
      );
    }

    localStorage.clear();

    store.subscribe(() => {
      localStorage["mentatus"] = JSON.stringify(store.getState());
    });

    // Ð±ÐµÐ· localStorage
  } else {
    store = createStore(
      rootReducer,
      composeWithDevTools(applyMiddleware(thunk))
    );
  }

  // hot reload
  if (module.hot) {
    module.hot.accept("./reducers", () => {
      const nextRootReducer = require("./reducers");
      store.replaceReducer(nextRootReducer);
    });
  }
  // console.log(store.getState())
  return store;
}
